<template>
  <div class="table">
    {{ id }}
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-tickets"></i> Collections Information
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          size="mini"
          class="handle-del mr10"
          @click="delAll"
          >Batch Delete</el-button
        >
        <el-input
          v-model="select_word"
          size="mini"
          placeholder="select keywords"
          class="handle-input mr10"
        ></el-input>
      </div>
      <el-table
        :data="tableData"
        size="mini"
        border
        style="width: 100%"
        ref="multipleTable"
        @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="50"></el-table-column>
        <el-table-column prop="name" label="Singer-song"></el-table-column>
        <el-table-column label="Operation" width="85">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row)"
              >Delete</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog title="Hint" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt" align="center">
        Delete is unrecoverable, is it determined to delete?
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="delVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="deleteRow"
          >Delete</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { HttpManager } from "../api/index";

export default {
  name: "collect-page",
  mixins: [mixin],
  props: ["id"],
  data() {
    return {
      tableData: [],
      tempDate: [],
      tempId: [],
      multipleSelection: [],
      delVisible: false,
      select_word: "",
      idx: -1
    };
  },
  watch: {
    select_word: function() {
      if (this.select_word === "") {
        this.tableData = this.tempDate;
      } else {
        this.tableData = [];
        for (let item of this.tempDate) {
          if (item.name.includes(this.select_word)) {
            this.tableData.push(item);
          }
        }
      }
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      HttpManager.getCollectionOfUser(this.$route.query.id)
        .then(res => {
          this.tableData = [];
          for (let item of res) {
            this.getSongList(item.songId);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getSongList(id) {
      HttpManager.getSongOfId(id)
        .then(res => {
          this.tableData.push(res[0]);
          this.tempDate.push(res[0]);
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteRow() {
      HttpManager.deleteCollection(this.$route.query.id, this.idx.id)
        .then(res => {
          if (res) {
            this.getData();
            this.notify("Successfully deleted", "success");
          } else {
            this.notify("Failed to delete this song", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.delVisible = false;
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
</style>



